// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {
  Vuetify,
  VApp,
  VList,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  transitions,
  VCard,
  VAvatar,
  VTextField,
  VSelect,
  VForm,
  VTooltip,
  VDialog,
  VTabs,
  VBadge,
  VDataTable
} from 'vuetify'
import '../node_modules/vuetify/src/stylus/app.styl'

Vue.use(Vuetify, {
  theme: {
    primary: '#ff5722',
    secondary: '#BDBDBD'
  },
  components: {
    VApp,
    VList,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    transitions,
    VCard,
    VAvatar,
    VTextField,
    VSelect,
    VForm,
    VTooltip,
    VDialog,
    VTabs,
    VBadge,
    VDataTable
  }
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



